import React, { useEffect, useState } from 'react'
import { CircularProgressbar } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import { calculateFilledPercentage } from '../../../../src/utils/common';
import CustomError from '../../globalComponents/CustomError';
import { CustomButton } from '../../globalComponents';
import { validateSchema } from '../../../../src/utils/ValidationHelper';
import { addSocialDetailsSchema } from '../../../../src/utils/ValidationSchema';

const SocialDetails = (props) => {
  const { addSocialDetails, onClickActive, profileData, getPreviousObjectKeyValue} = props;
  const [errors, setErrors] = useState({});
  const [socialDetails, setSocialDetails] = useState(
    {
      "linkedin_id": '',
      "facebook_id": '',
      "twitter_id": '',
      "instagram_id": '',
    }
  );


  useEffect(()=>{
    if(profileData){
      setSocialDetails(
        {
          "linkedin_id": profileData?.linkedin_id||'',
          "facebook_id": profileData?.facebook_id||'',
          "twitter_id": profileData?.twitter_id||'',
          "instagram_id": profileData?.instagram_id||'',
        }
      );
    }
  },[profileData])

  const percentage = calculateFilledPercentage(socialDetails);

  const handleFormData = async (e) => {
    errors && setErrors({});
    const { name, value } = e.target
    setSocialDetails({ ...socialDetails, [name]: value })
  };

  const onSubmitForm = async () => {
    const validationResponse = await validateSchema(addSocialDetailsSchema, socialDetails);
    if (validationResponse.isValid) {
      setErrors({});
      addSocialDetails(socialDetails)
    } else {
      setErrors(validationResponse.errors);
    }
  };

  return (
    <>
      <div className="rightSec">
        <div className="formPanel detailInner">
          <div className="DetailHeader">
            <div>
            <h6 className="m-0 fw-semibold">Social Details</h6>
            <small>Please fill all social details to proceed to the next step.</small>
            </div>
            <div className="position-relative">
              <div style={{ width: 70, height: 70 }}>
                <CircularProgressbar value={percentage} text={`${percentage}%`} styles={{ path: { stroke: "green" } }} />;
              </div>
            </div>
          </div>
          <div className="p-3">
            <div className="row">
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <label htmlFor="">LinkedIn ID</label>
                  <input
                    type="text"
                    name="linkedin_id"
                    value={socialDetails?.linkedin_id}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter LinkedIn ID"
                  />
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <label htmlFor="">Instagram ID</label>
                  <input
                    type="text"
                    name="instagram_id"
                    value={socialDetails?.instagram_id}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter Instagram ID"
                  />
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <label htmlFor="">Facebook ID</label>
                  <input
                    type="text"
                    name="facebook_id"
                    value={socialDetails?.facebook_id}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter Facebook ID"
                  />
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group mb-3">
                  <label htmlFor="">Twitter ID</label>
                  <input
                    type="text"
                    name="twitter_id"
                    value={socialDetails?.twitter_id}
                    onChange={(e) => handleFormData(e)}
                    className="form-control"
                    placeholder="Enter Twitter ID"
                  />
                </div>
              </div>
              <div className='d-flex' style={{ justifyContent: "space-between" }}>
                <div className="mt-3 mb-3">
                  <CustomButton
                    text="Back"
                    classNames="btn btn-org-gradient"
                    handleClick={() => getPreviousObjectKeyValue("short_name","social_details")}
                  />
                </div>
                <div className="mt-3 mb-3">
                  <CustomButton
                    text={"Save & Next"}
                    classNames="btn btn-org-gradient"
                    handleClick={() => onSubmitForm()}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div></>
  )
}

export default SocialDetails