import React, { useEffect, useState } from 'react';
import { OnboardingSidebar, PersonalDetails, FamilyDetails, EducationDetails, BankDetails, SocialDetails, WorkExperienceDetails, ReferenceDetails, DocumentDetails, PolicyDetails, AadharVerifyModal } from '../components/employee/onboarding-process'
import { useDispatch, useSelector } from 'react-redux';
import { employeeActions, onboardingActions, policiesActions } from '../core/resource/actions';
const Onboarding = () => {
    const [filledFormTabs, setFilledFormTabs] = useState([])
    const [allPolicyListData, setAllPolicyListData] = useState('')
    const [allEmployeeDocs, setAllEmployeeDocs] = useState('')
    const [profileData,setProfileData]=useState('')
    const [allOnboardingSteps,setAllOnboardingSteps]=useState('')
    const [isActive, setIsActive] = useState('');
    const [userVerifiedDetails, setUserVerifiedDetails] = useState();
    const [policyAccept,setPolicyAccept]=useState('')
    const [aadhaarVerify,setAadhaarVerify]=useState('')

    const onClickActive = (val) => {
        setIsActive(val)
    }
    const dispatch = useDispatch();
    const {
        addPersonalDetailsPending,
        addPersonalDetailsRes,
        addPersonalDetailsError,

        addFamilyDetailsPending,
        addFamilyDetailsRes,
        addFamilyDetailsError,

        addEducationDetailsPending,
        addEducationDetailsRes,
        addEducationDetailsError,

        addBankDetailsPending,
        addBankDetailsRes,
        addBankDetailsError,

        addSocialDetailsPending,
        addSocialDetailsRes,
        addSocialDetailsError,

        addWorkExperienceDetailsPending,
        addWorkExperienceDetailsRes,
        addWorkExperienceDetailsError,

        addReferenceDetailsPending,
        addReferenceDetailsRes,
        addReferenceDetailsError,

        addDocumentDetailsPending,
        addDocumentDetailsRes,
        addDocumentDetailsError,

        getAllOnboardingDetailsPending,
        getAllOnboardingDetailsRes,
        getAllOnboardingDetailsError,
   
        getAllEmpDocsPending,
        getAllEmpDocsRes,
        getAllEmpDocsError,
    } = useSelector(state => state.onboarding);
    const {
        getProfilePending,
        getProfileRes,
        getProfileError,

       aadhaarVerifyPending,
       aadhaarVerifyRes,
       aadhaarVerifyError,
    } = useSelector(state => state.employee)

    const {
        getPolicyListPending,
        getPolicyListRes,
        getPolicyListError,

        acceptPolicyByUserPending,
        acceptPolicyByUserRes,
        acceptPolicyByUserError,
    } = useSelector(state => state.policies)

    const addPersonalDetails = (params) => {
        const payload = {
            query_type: "personal_details",
            step_id: "1",
            ...params
        }
        dispatch(onboardingActions.addPersonalDetailsRequest(payload))
    };

    const getNextObjectKeyValue = (key, value) => {
        const index = allOnboardingSteps.findIndex(obj => obj[key] === value);
        if (index !== -1 && index < allOnboardingSteps.length - 1) {
          let next_value = allOnboardingSteps[index + 1][key] ?? null;
          onClickActive(next_value)
        }
        return null;
    };

    const getPreviousObjectKeyValue = (key, value) => {
        const index = allOnboardingSteps.findIndex(obj => obj[key] === value);
        if (index > 0) {
          let previous_value = allOnboardingSteps[index - 1][key] ?? null;
          onClickActive(previous_value)
        }
        return null;
      }

    useEffect(() => {
        setIsActive(allOnboardingSteps[0]?.short_name)
    },[allOnboardingSteps])

    useEffect(() => {
        if(profileData?.is_aadhar_verified){
            setFilledFormTabs(pre => [...pre, "aadhaar_verification"])
        }if(profileData?.name){
            setFilledFormTabs(pre => [...pre, "personal_details"])
        }if(profileData?.fathers_name){
            setFilledFormTabs(pre => [...pre, "family_details"])
        }if(profileData?.education_details?.length > 0){
            setFilledFormTabs(pre => [...pre, "education_details"])
        }if(profileData?.ifsc_code){
            setFilledFormTabs(pre => [...pre, "bank_details"])
        }if(profileData?.linkedin_id){
            setFilledFormTabs(pre => [...pre, "social_details"])
        }if(profileData?.work_experience_details?.length > 0){
            setFilledFormTabs(pre => [...pre, "work_experience_details"])
        }if(profileData?.reference_details?.length > 0){
            setFilledFormTabs(pre => [...pre, "reference_details"])
        }if(allEmployeeDocs?.length > 0){
            setFilledFormTabs(pre => [...pre, "upload_documents"])
        }if(profileData?.accepted_policies){
            setFilledFormTabs(pre => [...pre, "policy"])
        }
    },[profileData])

    const addFamilyDetails = (params) => {
        const payload = {
            query_type: "family_details",
            step_id: "2",
            ...params
        }
        dispatch(onboardingActions.addFamilyDetailsRequest(payload))
    };
    const addEductionDetails = (params) => {
        const payload = {
            query_type: "education_details",
            step_id: "3",
            ...params
        }
        dispatch(onboardingActions.addEducationDetailsRequest(payload))
    };

    const addBankDetails = (params) => {
        const payload = {
            query_type: "bank_details",
            step_id: "4",
            ...params
        }
        dispatch(onboardingActions.addBankDetailsRequest(payload))
    };

    const addSocialDetails = (params) => {
        const payload = {
            query_type: "social_details",
            step_id: "5",
            ...params
        }
        dispatch(onboardingActions.addSocialDetailsRequest(payload))
    };

    const addReferenceDetails = (params) => {
        const payload = {
            query_type: "reference_details",
            step_id: "7",
            reference_details: [params]
        }
        dispatch(onboardingActions.addReferenceDetailsRequest(payload))
    };

    const addWorkExperienceDetails = (params) => {
        const payload = {
            query_type: "work_experience_details",
            step_id: "6",
            ...params
        }
        dispatch(onboardingActions.addWorkExperienceDetailsRequest(payload))

    };

    const uploadDocuments = (params) => {
        let formData = new FormData()
        formData.append("aadhar_card", params?.aadhar_card || null)
        formData.append("aadhar_card_back", params?.aadhar_card_back || null)
        formData.append("passport", params?.passport || null)
        formData.append("passport_back", params?.passport_back || null)
        formData.append("pancard", params?.pancard || null)
        formData.append("driving_license", params?.driving_license || null)
        formData.append("resume", params?.resume || null)
        formData.append("passport_size_photo", params?.passport_size_photo || null)
        formData.append("secondary_marksheet", params?.secondary_marksheet || null)
        formData.append("senior_secondary_marksheet", params?.senior_secondary_marksheet || null)
        formData.append("ITI_marksheet", params?.ITI_marksheet || null)
        formData.append("diploma_marksheet", params?.diploma_marksheet || null)
        formData.append("graduation_marksheet", params?.graduation_marksheet || null)
        formData.append("post_graduation_marksheet", params?.post_graduation_marksheet || null)
        formData.append("bank_passbook", params?.bank_passbook || null)
        formData.append("previous_company_experience_letter", params?.previous_company_experience_letter || null)
        formData.append("previous_company_payslip", params?.previous_company_payslip || null)
        formData.append("police_verification", params?.police_verification || null)
        formData.append("medical_certificate", params?.medical_certificate || null)
        formData.append("bgv_certificate", params?.bgv_certificate || null)
        formData.append("query_type", "document_details")
        formData.append("step_id", "8")

        dispatch(onboardingActions.addDocumentDetailsRequest(formData))

    };
    useEffect(() => {
        // personal details
        if (!addPersonalDetailsPending) {
            if (addPersonalDetailsError) {
                dispatch(onboardingActions.handleClearPersonalDetails('HANDLE_CLEAR_ADD_PERSONAL_DETAILS'))
                return;
            }
            if (addPersonalDetailsRes) {
                if (addPersonalDetailsRes?.code === 200) {
                    getNextObjectKeyValue("short_name","personal_details")
                    dispatch(onboardingActions.handleClearPersonalDetails('HANDLE_CLEAR_ADD_PERSONAL_DETAILS'))
                    setFilledFormTabs(pre => [...pre, "personal_details"])
                    getProfileData()

                }
            }
        };
        // family details

        if (!addFamilyDetailsPending) {
            if (addFamilyDetailsError) {
                dispatch(onboardingActions.handleClearFamilyDetails('HANDLE_CLEAR_ADD_FAMILY_DETAILS'))
                return;
            }
            if (addFamilyDetailsRes) {
                if (addFamilyDetailsRes?.code === 200) {
                    getNextObjectKeyValue("short_name","family_details")
                    dispatch(onboardingActions.handleClearFamilyDetails('HANDLE_CLEAR_ADD_FAMILY_DETAILS'))
                    setFilledFormTabs(pre => [...pre, "family_details"])
                    getProfileData()
                }
            }
        }
        if (!addEducationDetailsPending) {
            if (addEducationDetailsError) {
                dispatch(onboardingActions.handleClearEducationDetails('HANDLE_CLEAR_ADD_EDUCATION_DETAILS'))
                return;
            }
            if (addEducationDetailsRes) {
                if (addEducationDetailsRes?.code === 200) {
                    getNextObjectKeyValue("short_name","education_details")
                    dispatch(onboardingActions.handleClearEducationDetails('HANDLE_CLEAR_ADD_EDUCATION_DETAILS'))
                    setFilledFormTabs(pre => [...pre, "education_details"])
                    getProfileData()
                }
            }
        }
        // bank details
        if (!addBankDetailsPending) {
            if (addBankDetailsError) {
                dispatch(onboardingActions.handleClearBankDetails('HANDLE_CLEAR_ADD_BANK_DETAILS'))
                return;
            }
            if (addBankDetailsRes) {
                if (addBankDetailsRes?.code === 200) {
                    getNextObjectKeyValue("short_name","bank_details")
                    dispatch(onboardingActions.handleClearBankDetails('HANDLE_CLEAR_ADD_BANK_DETAILS'))
                    setFilledFormTabs(pre => [...pre, "bank_details"])
                    getProfileData()
                }
            }
        }
        // social details
        if (!addSocialDetailsPending) {
            if (addSocialDetailsError) {
                dispatch(onboardingActions.handleClearSocialDetails('HANDLE_CLEAR_ADD_SOCIAL_DETAILS'))
                return;
            }
            if (addSocialDetailsRes) {
                if (addSocialDetailsRes?.code === 200) {
                    getNextObjectKeyValue("short_name","social_details")
                    dispatch(onboardingActions.handleClearSocialDetails('HANDLE_CLEAR_ADD_SOCIAL_DETAILS'))
                    setFilledFormTabs(pre => [...pre, "social_details"])
                    getProfileData()
                }
            }
        }
        // social details
        if (!addWorkExperienceDetailsPending) {
            if (addWorkExperienceDetailsError) {
                dispatch(onboardingActions.handleClearWorkExperienceDetails('HANDLE_CLEAR_ADD_WORK_EXPERIENCE_DETAILS'))
                return;
            }
            if (addWorkExperienceDetailsRes) {
                if (addWorkExperienceDetailsRes?.code === 200) {
                    getNextObjectKeyValue("short_name","work_experience_details")
                    dispatch(onboardingActions.handleClearWorkExperienceDetails('HANDLE_CLEAR_ADD_WORK_EXPERIENCE_DETAILS'))
                    setFilledFormTabs(pre => [...pre, "work_experience_details"])
                    getProfileData()
                }
            }
        }
        // reference details
        if (!addReferenceDetailsPending) {
            if (addReferenceDetailsError) {
                dispatch(onboardingActions.handleClearReferenceDetails('HANDLE_CLEAR_ADD_REFERENCE_DETAILS'))
                return;
            }
            if (addReferenceDetailsRes) {
                if (addReferenceDetailsRes?.code === 200) {
                    getNextObjectKeyValue("short_name","reference_details")
                    dispatch(onboardingActions.handleClearReferenceDetails('HANDLE_CLEAR_ADD_REFERENCE_DETAILS'))
                    setFilledFormTabs(pre => [...pre, "reference_details"])
                    getProfileData()
                }
            }
        }

        // document details
        if (!addDocumentDetailsPending) {
            if (addDocumentDetailsError) {
                dispatch(onboardingActions.handleClearDocumentDetails('HANDLE_CLEAR_ADD_DOCUMENT_DETAILS'))
                return;
            }
            if (addDocumentDetailsRes) {
                if (addDocumentDetailsRes?.code === 200) {
                    getNextObjectKeyValue("short_name","upload_documents")
                    dispatch(onboardingActions.handleClearDocumentDetails('HANDLE_CLEAR_ADD_DOCUMENT_DETAILS'))
                    setFilledFormTabs(pre => [...pre, "upload_documents"])
                    getAllEmployeeDocs()
                    getProfileData()
                }
            }
        }
    }, [
        addPersonalDetailsRes,
        addPersonalDetailsError,
        addPersonalDetailsPending,

        addFamilyDetailsRes,
        addFamilyDetailsError,
        addFamilyDetailsPending,

        addEducationDetailsPending,
        addEducationDetailsRes,
        addEducationDetailsError,


        addBankDetailsRes,
        addBankDetailsError,
        addBankDetailsPending,

        addSocialDetailsRes,
        addSocialDetailsError,
        addSocialDetailsPending,

        addWorkExperienceDetailsPending,
        addWorkExperienceDetailsRes,
        addWorkExperienceDetailsError,

        addReferenceDetailsRes,
        addReferenceDetailsError,
        addReferenceDetailsPending,

        addDocumentDetailsRes,
        addDocumentDetailsError,
        addDocumentDetailsPending,
    ]);

    /* Get All Policy  List*/
    const handleGetPolicyList = () => {
        dispatch(policiesActions.getPolicyListRequest());
    }

    useEffect(() => {
        if (!getPolicyListPending) {
            if (getPolicyListError) {
                return;
            }
            if (getPolicyListRes) {
                if (getPolicyListRes?.code === 200) {
                    const getPolicyListResData = getPolicyListRes?.data
                    setAllPolicyListData(getPolicyListResData)
                    getProfileData()
                }
            }
        }
    }, [
        getPolicyListRes,
        getPolicyListError,
        getPolicyListPending
    ]);

    useEffect(() => {
        handleGetPolicyList()
        getProfileData()
        getOnboardingSteps()
        getAllEmployeeDocs()
    }, [])


    const getProfileData = () => {
        dispatch(employeeActions.getProfileRequest())
    }

    useEffect(() => {
        if (!getProfilePending) {
            if (getProfileError) {
                return;
            }
            if (getProfileRes) {
                if (getProfileRes?.code === 200) {
                    setProfileData(getProfileRes?.data?.[0])
                }
            }
        }
    }, [
        getProfileRes,
        getProfileError,
        getProfilePending
    ]);

    const getOnboardingSteps = () => {
        let data = {
            is_active: true,
        }
        dispatch(onboardingActions.getAllOnboardingDetailsRequest(data))
    }

    useEffect(() => {
        if (!getAllOnboardingDetailsPending) {
            if (getAllOnboardingDetailsError) {
                return;
            }
            if (getAllOnboardingDetailsRes) {
                if (getAllOnboardingDetailsRes?.code === 200) {
                    setAllOnboardingSteps(getAllOnboardingDetailsRes?.data)
                }
            }
        }
    }, [
        getAllOnboardingDetailsRes,
        getAllOnboardingDetailsError,
        getAllOnboardingDetailsPending
    ]);

    const updatePolicySign = (data) => {
        dispatch(policiesActions.acceptPolicyByUserRequest(data))
    }

    useEffect(() => {
        if (!acceptPolicyByUserPending) {
            if (acceptPolicyByUserError) {
                return;
            }
            if (acceptPolicyByUserRes) {
                if (acceptPolicyByUserRes?.code === 200) {
                    setPolicyAccept(acceptPolicyByUserRes?.data)
                    setFilledFormTabs(pre => [...pre, "policy"])   
                }
            }
        }
    }, [
        acceptPolicyByUserRes,
        acceptPolicyByUserError,
        acceptPolicyByUserPending
    ]);

    const updateAadharVerify = (verified) => {
        let payload = {
            employee_id: profileData?._id,
            is_aadhar_verified: verified
        }
        dispatch(employeeActions.aadhaarVerifyRequest(payload))
    }

    useEffect(() => {
        if (!aadhaarVerifyPending) {
            if (aadhaarVerifyError) {
                return;
            }
            if (aadhaarVerifyRes) {
                if (aadhaarVerifyRes?.code === 200) {
                    setAadhaarVerify(aadhaarVerifyRes?.data)
                    setFilledFormTabs(pre => [...pre, "aadhaar_verification"])
                    getProfileData()
                }
            }
        }
    }, [
        aadhaarVerifyRes,
        aadhaarVerifyError,
        aadhaarVerifyPending
    ]);

    const getAllEmployeeDocs = () => {
        dispatch(onboardingActions.getAllEmpDocsRequest())
    }

    useEffect(() => {
        if (!getAllEmpDocsPending) {
            if (getAllEmpDocsError) {
                return;
            }
            if (getAllEmpDocsRes) {
                if (getAllEmpDocsRes?.code === 200) {
                    setAllEmployeeDocs(getAllEmpDocsRes?.data)
                }
            }
        }
    }, [
        getAllEmpDocsRes,
        getAllEmpDocsError,
        getAllEmpDocsPending
    ]);

    const renderSubComponents = () => {
        const componentMap = {
            'aadhaar_verification': (
              <AadharVerifyModal
                setUserVerifiedDetails={setUserVerifiedDetails}
                onClickActive={onClickActive}
                updateAadharVerify={updateAadharVerify}
              />
            ),
            'personal_details': (
              <PersonalDetails
                addPersonalDetails={addPersonalDetails}
                onClickActive={onClickActive}
                getPreviousObjectKeyValue={getPreviousObjectKeyValue}
                profileData={profileData}
                userVerifiedDetails={userVerifiedDetails}
              />
            ),
            'family_details': (
              <FamilyDetails
                addFamilyDetails={addFamilyDetails}
                onClickActive={onClickActive}
                getPreviousObjectKeyValue={getPreviousObjectKeyValue}
                profileData={profileData}
              />
            ),
            'education_details': (
              <EducationDetails
                addEductionDetails={addEductionDetails}
                onClickActive={onClickActive}
                getPreviousObjectKeyValue={getPreviousObjectKeyValue}
                profileData={profileData}
              />
            ),
            'bank_details': (
              <BankDetails
                addBankDetails={addBankDetails}
                onClickActive={onClickActive}
                getPreviousObjectKeyValue={getPreviousObjectKeyValue}
                profileData={profileData}
              />
            ),
            'social_details': (
              <SocialDetails
                addSocialDetails={addSocialDetails}
                onClickActive={onClickActive}
                getPreviousObjectKeyValue={getPreviousObjectKeyValue}
                profileData={profileData}
              />
            ),
            'work_experience_details': (
              <WorkExperienceDetails
                addWorkExperienceDetails={addWorkExperienceDetails}
                onClickActive={onClickActive}
                getPreviousObjectKeyValue={getPreviousObjectKeyValue}
                profileData={profileData}
              />
            ),
            'reference_details': (
              <ReferenceDetails
                addReferenceDetails={addReferenceDetails}
                onClickActive={onClickActive}
                getPreviousObjectKeyValue={getPreviousObjectKeyValue}
                profileData={profileData}
              />
            ),
            'upload_documents': (
              <DocumentDetails
                uploadDocuments={uploadDocuments}
                onClickActive={onClickActive}
                getPreviousObjectKeyValue={getPreviousObjectKeyValue}
              />
            ),
            'policy': (
              <PolicyDetails
                allPolicyListData={allPolicyListData}
                updatePolicySign={updatePolicySign}
                profileData={profileData}
              />
            ),
          };
        return componentMap[isActive?.toLowerCase()] || null;
        };

    return (
        <>
            <div class="container-fluid">
                <div class="sidebarDoc">
                    <div class="row px-0 ">
                        <OnboardingSidebar onClickActive={onClickActive} isActive={isActive} filledFormTabs={filledFormTabs} allOnboardingSteps={allOnboardingSteps}/>
                        <div class="col-md-9 px-0 mainoverflow">
                            {renderSubComponents()}
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default Onboarding